<template>
  <el-tabs
    v-bind="attrs"
    v-model="currentGroupId"
    v-on="tabOn"
  >
    <el-tab-pane
      :key="item.groupId"
      :label="item.groupLabel"
      :name="item.groupId"
      v-for="item of computedGroups"
    >
      <ele-form
        v-bind="item.form"
        v-if="item.groupId === currentGroupId"
        v-on="item.on"
      />
    </el-tab-pane>
  </el-tabs>
</template>

<script>
import utils from './utils'

export default {
  name: 'EleFormGroup',
  inheritAttrs: false,
  props: {
    // tab https://element.eleme.cn/#/zh-CN/component/tabs#tabs-attributes
    tabAttrs: Object,
    // tab, https://element.eleme.cn/#/zh-CN/component/tabs#tabs-events
    tabOn: Object,
    // 
    groups: {
      type: Array,
      required: true
    },
    // tab
    activeGroupId: [String, Number]
  },
  computed: {
    // tabs
    attrs () {
      return Object.assign({}, { type: 'border-card' }, this.tabAttrs)
    },
    // form
    computedGroups () {
      return this.groups.map((item) => {
        item.form = Object.assign({}, this.$attrs, item.form)
        item.on = Object.assign({}, this.$listeners, item.on)
        return item
      })
    }
  },
  data () {
    return {
      currentGroupId: ''
    }
  },
  methods: {},
  created () {
    // 
    if (utils.isDef(this.activeGroupId)) {
      this.currentGroupId = this.activeGroupId
    } else {
      // groups
      if (this.groups.length) {
        this.currentGroupId = this.groups[0].groupId
      }
    }
  }
}
</script>
