<template>
  <el-input
    :class="desc.class"
    :style="desc.style"
    @input="handleChange"
    placeholder="URL"
    v-bind="desc.attrs"
    v-model="newValue"
    v-on="desc.on"
  >
    <template slot="prepend">Http://</template>
    <template slot="append">.com</template>
  </el-input>
</template>

<script>
import formMixin from '../lib/mixins/formMixin'
export default {
  name: 'custom-url', // name , ,  name 
  mixins: [formMixin] //  mixin , 
}
</script>
